import React, { Component } from 'react';
import Saying from './saying'
import '../assets/home.scss'
import socket from '../socket'
import {toBottom} from '../tool'
class Home extends Component{
    constructor(){
        super();
        this.getNewMsg=this.getNewMsg.bind(this);
        this.getUsernameColor=this.getUsernameColor.bind(this);
        this.state={
            allMsg:[],
            nickname:'',
            colors:[ 
                '#e21400', '#91580f', '#f8a700', '#f78b00',
                '#58dc00', '#287b00', '#a8f07a', '#4ae8c4',
                '#3b88eb', '#3824aa', '#a700ff', '#d300e7'
            ]
        }
    }
    componentDidMount(){
        var that=this;
        socket.on('newMsg',function(nickname,msg){
            that.state.allMsg.push(that.displayNewMsg({nickname,msg:':'+msg,color:that.getUsernameColor(nickname),type:'msg'}));
            that.setState({allMsg:that.state.allMsg});
            toBottom()
        })
        socket.on('system',function(nickname, num, type){
            that.state.allMsg.push(that.displayNewMsg({nickname,type:'system',num,msg:type==='login'?'joined':'left'}));
            that.setState({allMsg:that.state.allMsg});
            toBottom()
        })
    }
    getUsernameColor(username){
        // Compute hash code
        var hash = 7;
        for (var i = 0; i < username.length; i++) {
            hash = username.charCodeAt(i) + (hash << 5) - hash;
        }
        // Calculate color
        var index = Math.abs(hash % this.state.colors.length);
        return this.state.colors[index];
    }
    async getNewMsg(msg){
        var nickname=document.getElementById('header').textContent;
        //状态改变需要时间
        await this.state.allMsg.push(this.displayNewMsg({nickname,msg:':'+msg,color:this.getUsernameColor(nickname),type:'msg'})); 
        this.setState({allMsg:this.state.allMsg});
        toBottom();
    }
    displayNewMsg(data){
        return {
            nickname:data.nickname,
            msg:data.msg,
            color:data.type!=='msg'?'gray':data.color,
            num:data.num?data.num:null,
            classname:data.type
        }
    }
    render(){
        return(
            <div className="home">
                <div className="content" id="content">
                    <ul>
                        {
                            this.state.allMsg.map((msgObj,index)=>
                                <li className={msgObj.classname} key={index}>
                                    <span style={{color:msgObj.color}}>{msgObj.nickname}</span>
                                    {msgObj.msg}
                                    <p>{msgObj.num?'there are '+msgObj.num+' participants':''}</p>
                                </li>
                            )
                        }
                    </ul>
                </div>
                <Saying onNewMsg={this.getNewMsg}/>
            </div>
        )
    }
}

export default Home



