const toBottom=()=>{
    var content=document.getElementById('content');
    if(!content){
        console.log('没有发现聊天窗口');
        return
    }
    if(content.offsetHeight<content.scrollHeight){
        content.scrollTop=content.scrollHeight;
    }
}
export {toBottom}


