import React, { Component } from 'react';
import Begin from './components/begin'
import Home from './components/home'
import './App.scss';
import socket from './socket'
var components={
  begin:Begin,
  home:Home
}
class App extends Component {
  constructor(){
    super();
    this.handleViewChange=this.handleViewChange.bind(this);
    this.handlerLoginSuccess=this.handlerLoginSuccess.bind(this);
    this.state={
      view:'begin',
      nickname:''
    }
  }
  componentDidMount(){
    socket.on('loginSuccess',function(nickname){
        document.getElementById('header').innerHTML=nickname;
    })
  }
  handlerLoginSuccess(name){
    this.setState({nickname:name})
  }
  handleViewChange(v,pramas=null){
    this.setState({view:v})
    
  }
  render() {
    var Comp=components[this.state.view];
    return (
      <div className="App">
        <header className="header" id="header"></header>
        <Comp onViewChange={this.handleViewChange}/>
      </div>
    );
  }
}

export default App;



