import React, { Component } from 'react';
import '../assets/begin.scss'
import socket from '../socket'
import store from '../store'
class Begin extends Component{
    constructor(){
        super();
        this.handleLogin=this.handleLogin.bind(this)
    }
    componentDidMount(){
        document.getElementById('nickname').focus();
    }
    handleLogin(e){
        if(e.which!==13) return
        if(e.target.value.trim().length === 0){ 
            document.getElementById('nicktip').innerText='this nickname is empty '
            return
        }
        var nickname=document.getElementById('nickname').value
        socket.emit('login', nickname); //发射事件
        socket.on('nickExisted',()=>{
            document.getElementById('nicktip').innerText='this nickname is existed '
            store.nickname=nickname
        })
        socket.on('loginSuccess',()=>{
            this.props.onViewChange('home');
            
        })
    }
    render(){
        return(
            <div className="begin">
                <div className="nickbox">
                    <h2>What's your nickname?</h2>
                    <input type="text" id="nickname" onKeyPress={this.handleLogin}/>
                    <div className="nicktip" id="nicktip"></div>
                </div>
            </div>
        )
    }
}
export default Begin


