import React, { Component } from 'react';
import '../assets/saying.scss'
import socket from '../socket'
class Saying extends Component{
    constructor(){
        super();
        this.handleNewMsg=this.handleNewMsg.bind(this);
    }
    componentDidMount(){
        document.getElementById('msg').focus();
    }
    handleNewMsg(e){
        if(e.which!==13) return
        if(e.target.value.trim().length === 0) return
        socket.emit('postMsg', e.target.value);
        this.props.onNewMsg(e.target.value);
        e.target.value='';
    }
    render(){
        return(
            <div className="saying">
                <input type="text" onKeyPress={this.handleNewMsg} id="msg"/>
            </div>
        )
    }
}
export default Saying


